<template>
  <div class="main">
    <Topslider></Topslider>
    <div class="container">
      <section class="py-3">
        <h2 class="page_title m-0"> PlayStation Store</h2>
        <p class="p-3"> Vue  SwitchPS43DS </p>
      </section>
      <section class="py-3">
        <h2 class="page_title m-0"></h2>
        <ProdSlider prodCategory="all"></ProdSlider>
      </section>
    </div>
  </div>
</template>

<script>
import Topslider from '@/components/Topslider';
import ProdSlider from '@/components/ProdSlider';
export default {
  data() {
    return {};
  },
  components: {
    Topslider,
    ProdSlider
  }
}
</script>



